<template>
  <div class='painel'>
  	<h2 class='painel-titulo' v-on:dblclick="hideShow = !hideShow">{{ titulo }}</h2>
    <transition name='painel-fade'>
    	<div class='painel-conteudo' v-show="hideShow">
  	  		<slot></slot>
    	</div>
    </transition>
  </div>
</template>

<script>
export default {
  props: ['titulo'],

  data() {
  	return {
  		hideShow: true
  	}
  }
}
</script>

<style scoped>
.painel {
    padding: 0 auto;
    border: solid 2px grey;
    display: inline-block;
    margin: 5px;
    box-shadow: 5px 5px 10px grey;
    width: 200px;
    height: 100%;
    vertical-align: top;
    text-align: center;
  }

  .painel .painel-titulo {
    text-align: center;
    border: solid 2px;
    background: lightblue;
    margin: 0 0 15px 0;
    padding: 10px;
    text-transform: uppercase;
  }

  * {
  	box-shadow: 5px 5px 5px;
  }

  .painel-fade-leave-active, .painel-fade-enter{
  	opacity: 0
  }

  .painel-fade-enter-active, .painel-fade-leave-active {
  	transition: opacity .4s
  }
  .painel-conteudo {
    overflow:hidden; 
  }
</style>



